import React, { Component } from "react";
import { Row, Col, Divider } from "antd";

import IntlMessages from "../../../../components/utility/intlMessages";
import PrintTable from "../../../Tables/antTables/PrintTable.style";
import PrintTransaction, {
  PrintField,
  FieldCol
} from "../../../App/PrintTransaction";
import ReactToPrint from "react-to-print";
import ButtonPrint from "../../../App/ButtonPrint";
import moment from "moment";
import { Signature } from "../../../App/app.style";
import NumberFormat from "../../../App/NumberFormat";
import getLanguage from "../../../App/MyLanguange";
import { getFileName } from "../../../../helpers/helper";
import { siteConfig } from "../../../../settings";

class Print extends Component {
  render() {
    const data = this.props.data ? this.props.data : [];

    let journal_details = [];

    if (data.journal_details && data.journal_details.length > 0) {
      journal_details = [...data.journal_details];
      // console.log(data)
      journal_details.push({
        id_journal_detail: 1,
        index_no: "Total " + getLanguage({ id: "debit" }),
        credit_amount: "Total " + getLanguage({ id: "debit" }),
        amount: data.total_debit_amount
      });
      journal_details.push({
        id_journal_detail: 2,
        index_no: "Total " + getLanguage({ id: "credit" }),
        credit_amount: "Total " + getLanguage({ id: "credit" }),
        amount: data.total_credit_amount
      });
    }

    let column = [
      {
        title: getLanguage({ id: "no." }),
        dataIndex: "index_no",
        align: "right",
        render: text => {
          if (isNaN(text)) {
            return {
              props: {
                colSpan: 6
              }
            };
          } else {
            return text;
          }
        }
      },
      {
        title: getLanguage({ id: "accountCode" }),
        dataIndex: "account.account_code",
        render: (text, row) => {
          if (isNaN(row.index_no)) {
            return {
              props: {
                colSpan: 0
              }
            };
          } else {
            return text;
          }
        }
      },
      {
        title: getLanguage({ id: "accountName" }),
        dataIndex: "account.account_name",
        render: (text, row) => {
          if (isNaN(row.index_no)) {
            return {
              props: {
                colSpan: 0
              }
            };
          } else if (text && text.length > 50) {
            return text.substring(0, 50) + "...";
          } else {
            return text;
          }
        }
      },
      {
        title: getLanguage({ id: "currency" }),
        dataIndex: "currency.currency_code",
        align: "center",
        render: (text, row) => {
          if (isNaN(row.index_no)) {
            return {
              props: {
                colSpan: 0
              }
            };
          } else {
            return text;
          }
        }
      },
      {
        title: getLanguage({ id: "rate" }),
        dataIndex: "currency_rate",
        align: "right",
        render: (text, row) => {
          if (isNaN(row.index_no)) {
            return {
              props: {
                colSpan: 0
              }
            };
          } else {
            return <NumberFormat value={text} />;
          }
        }
      },
      {
        title: getLanguage({ id: "debitAmount" }),
        dataIndex: "debit_amount",
        align: "right",
        render: (text, row) => {
          if (isNaN(row.index_no)) {
            return {
              props: {
                colSpan: 0
              }
            };
          } else {
            return <NumberFormat value={text} />;
          }
        }
      },
      {
        title: getLanguage({ id: "creditAmount" }),
        dataIndex: "credit_amount",
        align: "right",
        render: (text, row) => {
          if (isNaN(row.index_no)) {
            return text;
          } else {
            return <NumberFormat value={text} />;
          }
        }
      },
      {
        title: `${getLanguage({ id: "amount" })} (IDR)`,
        dataIndex: "amount",
        align: "right",
        render: text => <NumberFormat value={text} />
      }
      // {
      //   title: getLanguage({id: "sidebar.location"}),
      //   dataIndex: "location.location_name",
      //   width: "15%",
      //   align: "center",
      //   render: text => {
      //     if (text && text.length > 50) {
      //       return text.substring(0, 50) + "...";
      //     } else {
      //       return text;
      //     }
      //   }
      // },
    ];

    const { loading } = this.props;
    const leftSide = [
      {
        label: <IntlMessages id="locations.pageHeader" />,
        description: data.location ? data.location.location_name : null
      },
      {
        label: <IntlMessages id="totalAmount" />,
        description: data.total_debit_amount ? (
          <NumberFormat value={data.total_debit_amount} />
        ) : null
      }
    ];

    const rightSide = [
      {
        label: <IntlMessages id="note" />,
        description: data.note
      }
    ];

    const signatureData = [
      {
        label: <IntlMessages id="createdBy" />
      },
      {
        label: <IntlMessages id="confirmedBy" />
      }
    ];
    return (
      <>
        <PrintTransaction
          className="print-transaction"
          componentRef={ref => (this.componentRef = ref)}
          title={<IntlMessages id="sidebar.journal" />}
          subtitle={
            <>
              <Row>
                <Col>
                  <strong>{data.transaction_no}</strong>
                </Col>
              </Row>
              <Row style={{ marginTop: "10px" }}>
                <Col span={24}>
                  <PrintField
                    bold={true}
                    label={<IntlMessages id="date" />}
                    description={
                      data.transaction_date
                        ? moment(
                            data.transaction_date,
                            "YYYY-MM-DD HH:mm:ss"
                          ).format("DD MMMM YYYY, HH:mm:ss")
                        : null
                    }
                  />
                </Col>
              </Row>
            </>
          }
        >
          {/* Bagian list detail */}
          <Row style={{ marginTop: "24px" }}>
            <FieldCol leftSide={leftSide} rightSide={rightSide} />
          </Row>

          {/* Bagian Table */}
          <Row style={{ marginTop: "24px" }}>
            <Divider
              orientation="left"
              style={{
                fontSize: "12px",
                alignItems: "center",
                display: "flex"
              }}
            >
              <IntlMessages
                id="gLAccountDetails"
                style={{ marginTop: "-10" }}
              />
            </Divider>
            <Col span={24}>
              <PrintTable
                columns={column}
                dataSource={journal_details}
                rowKey={row => row.id_journal_detail}
                pagination={false}
              />
            </Col>
          </Row>

          {/* <Row type="flex" style={{ marginTop: "24px" }}>
            <Col span={12}>
              <TotalField data={addition} />
            </Col>
            <Col span={12}>
              <TotalField data={total} />
            </Col>
          </Row> */}

          <Row style={{ marginTop: "24px" }}>
            <Col span={24}>
              <Signature data={signatureData} />
            </Col>
          </Row>
        </PrintTransaction>

        <ReactToPrint
          trigger={() => (
            <ButtonPrint loading={loading} style={{ marginLeft: "5px" }} />
          )}
          content={() => this.componentRef}
          onBeforePrint={() =>
            (document.title = getFileName("journal", data.transaction_no, true))
          }
          onAfterPrint={() => (document.title = siteConfig.documentTitle)}
        />
      </>
    );
  }
}

export default Print;
