import React, { Component } from "react";

import moment from "moment"

import EditableTable from "./editableTable";

import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import { Form, Input, Icon, Select, Col, DatePicker, Modal, Popconfirm, Steps, Tooltip, Row, Divider } from "antd";
import { customDot } from "../../../App/app.style";
import notification from "../../../../components/notification";
import ModalCancel from "./modalCancel";
import getLanguage from "../../../App/MyLanguange";
// import { getCurrencyRate } from "../../../../helpers/helper";

const { Option } = Select;
const InputGroup = Input.Group;
const TextArea = Input.TextArea;
const Step = Steps.Step;

class JournalUpdateForm extends Component {
  state = {
    check: 1,
    clearJournalDetailsState: false,
    id_location: null,
    temp_journal_detail: null,
    temp_journal_details: [],
    journal_details: [],
    total_debit_amount: 0,
    total_credit_amount: 0,
    visibleModalCancel: false,
  }

  componentDidUpdate = () => {
    if (this.state.check === 1 && this.props.journal.journal !== null) {

      const { journal, accounts, locations } = this.props.journal;
      const { setFieldsValue } = this.props.form;


      setFieldsValue({
        id_location: journal.id_location,
        note: journal.note,
      })

      let journal_details = [];
      let total_debit_amount = 0;
      let total_credit_amount = 0;

      journal.journal_details.map((data, key) => {

        // const [account] = accounts.filter((x) =>
        //   data.sub_ledger_type !== 4
        //     ?
        //     x.id_account === data.id_account &&
        //     x.id_sub_ledger === data.id_sub_ledger
        //     :
        //     x.id_account === data.id_account

        // )
        const account = data.account
        //console.log(account);
        const [location] = locations.filter((x) =>
          x.id_location === data.id_location
        )

        let journal_detail = {
          id_account: data.id_account,
          id_currency: data.id_currency,
          id_sub_ledger: data.id_sub_ledger,
          sub_ledger_type: data.sub_ledger_type,
          sub_ledger_name: data.sub_ledger_name,
          account_code: account.account_code,
          account_name: account.account_name,
          debit_amount: parseFloat(data.debit_amount),
          credit_amount: parseFloat(data.credit_amount),
          id_currency: data.id_currency,
          currency_code: data.currency.currency_code,
          currency_rate: data.currency_rate,
          //currency_rate : getCurrencyRate(data.currency.currency_rate, getFieldValue("transaction_date")),
          is_base: account.currency.is_base,
          id_location: location.id_location,
          location_name: location.location_name,
          id_department: data.id_department,
          id_project: data.id_project,
          index_no: data.index_no,
        }

        journal_detail.amount = (parseFloat(data.debit_amount) + parseFloat(data.credit_amount)) * journal_detail.currency_rate

        journal_details.push(journal_detail);
        total_debit_amount += parseFloat(data.debit_amount) * parseInt(journal_detail.currency_rate)
        total_credit_amount += parseFloat(data.credit_amount) * parseInt(journal_detail.currency_rate)
        this.setState({
          id_location: location.id_location
        })
      })

      this.setState({
        temp_journal_details: journal_details,
        total_debit_amount,
        total_credit_amount,
        check: 0
      })
    }
  }

  handleModalCancel = () => {
    this.setState({
      visibleModalCancel: !this.state.visibleModalCancel
    })
  }

  handleSave = (journal_details, total_debit_amount, total_credit_amount) => {
    this.setState({
      journal_details: journal_details,
      total_debit_amount,
      total_credit_amount
    });
  };

  clearJournalDetail = (journal_details, total_debit_amount, total_credit_amount) => {

    this.setState({
      temp_journal_detail: null,
      journal_details: journal_details,
      total_debit_amount,
      total_credit_amount
    });
  };

  clearJournalDetails = (journal_details, total_debit_amount, total_credit_amount) => {

    this.setState({
      clearJournalDetailsState: false,
      temp_journal_details: [],
      journal_details: journal_details,
      total_debit_amount,
      total_credit_amount,
      check: -1
    });
  };

  handleChangeTransDate = () => {
    this.setState({
      temp_journal_details: this.state.journal_details,
      journal_details: this.state.temp_journal_details
    })
  }

  handleChangeGLAccount = (e) => {
    //console.log(e)
    const { accounts, suppliers } = this.props.journal;
    const [account] = accounts.filter(
      data => (data.account_code + " " + data.account_name) === e
    );

    let exist = true //indikator apakah punya sub ledger dengan currency yang sama
    let haveSup = true //inkator apakah punya subledger supplier


    if (account.sub_ledger_type === 1) {
      // cek apakah punya list supplier
      const findSup = suppliers.find(data => data.id_location === account.id_location)
      if (findSup) {
        // do nothing
      } else {
        haveSup = false
      }
    }

    if (!haveSup) {
      Modal.error({
        title: getLanguage({id: "error"}),
        content: getLanguage({id: "journals.accounts.help.supp"})
      });
    }
    else {
      this.setState({
        temp_journal_detail: account
      });
    }
  }

  handleChangeLocation = (value) => {
    const { setFieldsValue } = this.props.form;
    setFieldsValue({
      id_account: [],
      id_cash_bank: null
    })
    this.setState({
      id_location: value,
      clearJournalDetailsState: true
    })
  }


  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFieldsAndScroll((err, values) => {
      // const { getFieldValue } = this.props.form;
      const { messages } = this.props.journal;
      // console.log(getFieldValue('currency_amount'))
      // console.log(this.state.total_debit_amount);

      if (this.state.journal_details.length < 1) {
        notification("error", messages.gl_account)
      }
      else if (this.state.total_debit_amount === 0 || this.state.total_credit_amount === 0) {
        notification("error", messages.zero_amount)
      }
      else if (this.state.total_debit_amount !== this.state.total_credit_amount) {
        notification("error", messages.unbalance_amount)
      }
      else if (!err) {
        values.transaction_date = values.transaction_date.format("YYYY-MM-DD HH:mm:ss");
        values.journal_details = [];
        this.state.journal_details.map((detail) => {
          if (parseFloat(detail.debit_amount) === 0 && parseFloat(detail.credit_amount) === 0) {
            // Ha Kosong ...  
          }
          else {
            values.journal_details.push(detail)
          }
          if (!detail.sub_ledger_name) {
            detail.sub_ledger_name = null
          }
        })
        this.props.submit(values);
      }
    });
  };


  render() {
    // console.log(this.props);
    const { getFieldDecorator, getFieldValue } = this.props.form;
    const { journal, processJournal } = this.props;
    const { loading } = journal;

    const data = journal.journal ? journal.journal : [];

    let permissions = this.props.permissions;

    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };
    const tailFormItemLayout = {
      wrapperCol: {
        xs: {
          span: 24,
          offset: 0
        },
        sm: {
          span: 24,
          offset: 0
        }
      }
    };

    return (
      <Form layout="vertical" onSubmit={this.handleSubmit}>
        <ModalCancel
          visible={this.state.visibleModalCancel}
          handleModal={this.handleModalCancel}
          submit={this.props.handleSubmitCancel}
          loading={loading}
        />
        {/* <Divider orientation="left" style={{ fontSize: 14 }}>
           <IntlMessages id="journals.add.subtitle.data" />
         </Divider> */}
        <Row style={{ margin: "24px 0px" }} >
          <Col span={16} justify="center">
            <Steps size="small"
              progressDot={customDot}
              current={
                data.status === 4
                  ? 2
                  : data.status === 3
                    ? 3
                    : data.status - 1
              }
            >
              <Step title={getLanguage({ id: "open"})} />
              <Step title={getLanguage({ id: "processed"})} />
              <Step title={getLanguage({ id: "closed"})} />
              <Step title={getLanguage({ id: "cancelled"})} />
            </Steps>
          </Col>
        </Row>
        <div className="isoDetailDataFlex" style={{ padding: "0" }}>
          <div style={{ padding: "0 15px 0 0" }} className="isoColumn-2 isoFormGroupComp">

            <Form.Item
              {...formItemLayout}
              label={<IntlMessages id="journals.no" />}
            >
              {getFieldDecorator("transaction_no", {
                initialValue: data.transaction_no,

              })(<Input
                disabled={true}
              />)}
            </Form.Item>

            <InputGroup>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={
                    <IntlMessages id="purchaseOrders.transactionDate" />
                  }
                >
                  {getFieldDecorator("transaction_date", {
                    initialValue: moment(data.transaction_date, "YYYY-MM-DD HH:mm:ss"),
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      }
                    ]
                  })(
                    <DatePicker
                      style={{ width: "100%" }}
                      showTime
                      format="DD MMMM YYYY HH:mm:ss"
                      onChange={this.handleChangeTransDate}
                      onOpenChange={this.handleChangeTransDate}
                      disabled={
                        permissions.includes("Can Back Date")
                          ? data.status === 1
                            ? false
                            : true
                          : true
                      }
                      allowClear={false}
                      disabledDate={e => e.valueOf() > moment()}
                    />
                  )}
                </Form.Item>
              </Col>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={
                    this.state.id_location && this.state.journal_details.length > 0 && data.status === 1 ?
                      <Tooltip
                        placement="top"
                        title={
                          <IntlMessages id="journals.location.help" />
                        }
                      >
                        <IntlMessages id="locations.pageHeader" />
                        <Icon
                          style={{ marginLeft: "5px" }}
                          type="question-circle"
                          theme="filled"
                        />
                      </Tooltip>
                      : <IntlMessages id="locations.pageHeader" />
                  }
                >
                  {getFieldDecorator("id_location", {
                    //initialValue: data.id_location,
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      }
                    ]
                  })(
                    <Select
                      loading={this.props.journal.loading}
                      showSearch
                      optionFilterProp="children"
                      onChange={this.handleChangeLocation}
                      disabled={
                        data.status === 1
                          ? false
                          : true
                      }
                      style={{ width: "100%" }}
                    >
                      {journal.locations.map((data, key) => (
                        <Option key={key} value={data.id_location}>
                          {data.location_code + " (" + data.location_name + ")"}
                        </Option>
                      ))}
                    </Select>
                  )}
                </Form.Item>
              </Col>
            </InputGroup>

          </div>
          <div style={{ padding: "0 0 0 15px" }} className="isoColumn-2 isoFormGroupComp">

            <Form.Item
              {...formItemLayout}
              label={<IntlMessages id="note" />}
            >
              {getFieldDecorator("note", {
                //initialValue: data.note,
              })(<TextArea
                disabled={
                  data.status === 1
                    ? false
                    : true
                }
                autosize={{ minRows: 4 }}
              />)}
            </Form.Item>

          </div>
        </div>

        <Divider orientation="left" style={{ fontSize: 14 }}>
          <IntlMessages id="journals.add.subtitle.GLAccount" />
        </Divider>
        {
          data.status === 1
            ?
            (
              <Form.Item
                {...formItemLayout}
              //label={<IntlMessages id="GLAccount" />}
              >
                {getFieldDecorator("id_account", {
                })(
                  <Select
                    loading={this.props.journal.loading}
                    showSearch
                    optionFilterProp="children"
                    onChange={this.handleChangeGLAccount.bind(this)}
                  // onOpenChange={this.handleChangeGLAccount}
                  // disabled={used ? true : false}
                  >

                    {getFieldValue('id_location')
                      ?
                      journal.accounts.map((data, key) => {
                        if (data.id_location === getFieldValue('id_location') || !data.id_location) {
                          return <Option
                            key={key}
                            value={data.account_code + " " + data.account_name}
                          // disabled={
                          //   data.sub_ledger_type === 0
                          //     ? false
                          //     : true
                          // }
                          >
                            {data.account_code + " " + data.account_name}
                          </Option>
                        }
                      })
                      : null
                    }
                  </Select>
                )}
              </Form.Item>
            )
            : null
        }


        <EditableTable
          accounts={this.props.journal.accounts}
          cashbanks={this.props.journal.cashbanks}
          check={this.state.check}
          clearJournalDetailsState={this.state.clearJournalDetailsState}
          clearJournalDetail={this.clearJournalDetail}
          clearJournalDetails={this.clearJournalDetails}
          currencies={this.props.journal.currencies}
          customers={this.props.journal.customers}
          departments={this.props.journal.departments}
          handleSave={this.handleSave}
          id_location={getFieldValue('id_location')}
          journal_details={this.state.journal_details}
          locations={this.props.journal.locations}
          projects={this.props.journal.projects}
          status={data.status}
          suppliers={this.props.journal.suppliers}
          temp_journal_detail={this.state.temp_journal_detail}
          temp_journal_details={this.state.temp_journal_details}
          transaction_date={getFieldValue("transaction_date")}
        />


        <Form.Item {...tailFormItemLayout}>
          <Button
            type="primary"
            htmlType="submit"
            style={{ marginTop: "20px", marginRight: "5px" }}
            loading={loading}
            disabled={
              data.status === 1
                ? false
                : true
            }
          >
            <IntlMessages id="button.submit" />
          </Button>

          {/* Process */}
          <Popconfirm
            title={<IntlMessages id="confirmTransaction" />}
            onConfirm={() =>
              processJournal(data.id_journal)
            }
            okText={getLanguage({ id: "yes" })}
            cancelText={getLanguage({ id: "no" })}
          >
            <Button
              style={{ marginRight: "5px", padding: "0px 15px" }}
              disabled={
                data.status === 1 &&
                  permissions.includes("Confirm Journal")
                  ? false
                  : true
              }
              loading={loading}
            >
              <IntlMessages id="button.confirmTransaction" />
            </Button>
          </Popconfirm>

          {/* Cancel */}

          <Button
            style={{ marginRight: "5px", padding: "0px 15px" }}
            disabled={
              data.status === 2 &&
                permissions.includes("Cancel Journal")
                ? false
                : true
            }
            loading={loading}
            onClick={this.handleModalCancel}
          >
            <IntlMessages id="button.cancelTransaction" />
          </Button>

        </Form.Item>
      </Form>
    );
  }
}

const WrappedJournalUpdateForm = Form.create({ name: "update_journal" })(JournalUpdateForm);

export default WrappedJournalUpdateForm;
